body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Target the entire scrollbar */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background-color: #f2f2f2;
  }
  
  /* Thumb */
  ::-webkit-scrollbar-thumb {
    background-color: #66666662;
    border-radius: 5px;
  }
  
  /* Corner */
  ::-webkit-scrollbar-corner {
    background-color: #f2f2f2;
  }
  

.navbar{
    background-color: f7f6f2;
    border-bottom: 1px solid rgb(200, 200, 200);
}
.nav{
    min-width: 100%;
}
.slide {
    position: relative;
    overflow: hidden;
  }
  
  .text {
    position: relative;
    transition: 0.5s;
    color: rgb(130, 130, 130);
  }
  
  .slide::before {
    content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 130%;
  height: 55px;
  background-color: #007bff;
  -webkit-transform: translate(-110%, 0) skew(-30deg);
          transform: translate(-110%, 0) skew(-30deg);
  transition: 0.5s;
  }
  
  .slide:hover .text {
    color: #fff;
  }
  
  .slide:hover::before {
    -webkit-transform: translate(-5%, 0) skew(-15deg);
            transform: translate(-5%, 0) skew(-15deg);
  }
.model{
    min-height: 40vh;
        display: grid;
        align-items: center;     
}
.login_button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    border: 1px solid rgb(108, 108, 108);
    width: 100%;
    border: 0;
    padding: 15px;
    font-size: 14px;
    transition: all 0.3 ease;
    cursor: pointer;
    text-decoration: none;
    text-align: center;
    color: black;
    background-color: rgb(233, 233, 233);
  }
  .login_button:hover{
    color: none;
  }
  .text-wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .text-wrapper span{
    padding: 0px 8px;
    }

  .text-wrapper hr {
    width: 40%;
    height: 2px;
}
.product-list{
  top: 80%;
  position: absolute;
  max-height: 40vh;
  min-height: auto;
  background-color: white;
  z-index: 2;
  overflow-y: scroll;
  overflow-x: hidden;
}
.product-list li{
  list-style: none;
}
.liSearchProducts{
  padding: 5px 0px;
  margin: 2px;
  min-width: 100%;
  display: block;
  border-bottom: 1px solid rgb(223, 223, 223);
  color: black;
  text-decoration: none;
}
.prev {
    left: -30px;
  }
  
  .next {
    right: -30px;
  }
  .slider {
    position: relative;
  }
  
  .prev,
  .next {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 30px;
    height: 30px;
    cursor: pointer;
    z-index: 1;
    background-color: black;
    border-radius: 50%;
    color: white;
    font-weight: bolder;
    padding: 5px;
    font-size: 1.5rem;
  }
  
  .prev {
    left: 20px;
  }
  
  .next {
    right: 20px;
  }
  

  .item-link {
    position: relative;
    display: inline-block;
  }
  
  .item-link .item-info {
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    padding: 0px;
    text-align: center;
  }
  
  .item-link:hover .item-info {
    display: block;
  }
  
  .productSliderImage{
    width: 600px;
    height: auto;
  }

  @media screen and (max-width: 1200px) {
    .productSliderImage{
      width: 80%;
      height: auto;
    }
  }

.productDiv{
    min-width: 100%;
    max-height: 250px;
    min-height: 250px;
    border: 1px solid rgb(150, 150, 150);
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
    box-sizing: border-box;
    display: inline-block;
}

.LinkSeeMore{
    text-decoration: none;
    color: black;
